import { STATION_LIST_KNOWN } from "../actions/stationActions";

const InitialState = {
    stationList: [],
};

export default function reducer(state = InitialState, action: any) {
    switch (action.type) {
        case STATION_LIST_KNOWN:
            return {
                ...state,
                stationList: action.stationList,
            };
        default:
    }
    return state;
}
