import ApolloClient, { gql } from 'apollo-boost';
import { navigate } from "gatsby"
import fetch from 'isomorphic-fetch';
import * as loadingActions from './loadingActions';


export const STATION_LIST_KNOWN = 'STATION_LIST_KNOWN';

export const stationListKnown = (stationList) => ({
    type: STATION_LIST_KNOWN,
    stationList,
});

const client = new ApolloClient({
  uri: 'http://api.sewage.gnometech.no/graphql',
  fetch,
});

export const getUserStations: Function = (
) => (
  dispatch: Function,
  getState: Function
) => {
  dispatch(loadingActions.loadingStart());

  const token = localStorage.getItem('sewage:token')
  client
    .query({
      query: gql`
        {
          getStationListByUser(token: "${token}") {
            stationList {
              id
              name
              latitude
              longitude
            }
          }
        }`,
    })
    .then(result => {
      dispatch(loadingActions.loadingEnd());
      // if (result.data.login && result.data.login.errorMsg === 'ok') {
      dispatch(stationListKnown(result.data.getStationListByUser.stationList))
      // } else {
      // }

    });
};

export const login2 = () => ('login');
